import React from "react"

function EducationList() {
    const educations = [
        {
            'title': 'Master of Computer Science (MS)',
            'university': 'Cracow University of Technology',
            'years': '2008 – 2012 (Cracow, Poland)',
            'desc': 'Studied Applied Computer Science  on Faculty of Physics, Mathematics and Applied Computer Science. Thesis of MS work is "MVC model based framework build and implementation in web application"'
        },
        {
            'title': 'Bachelor of Engineering (BEng)',
            'university': 'Cracow University of Technology',
            'years': '2004 – 2008 (Cracow, Poland)',
            'desc': 'Studied Applied Computer Science on Faculty of Physics, Mathematics and Applied Computer Science. Thesis of BEng work is "A city information system"'
        }
    ];

    return educations.map((education) => <div className="item col-12 col-md-6">
        <div className="item-inner">
            <h3 className="degree">{education.title}</h3>
            <div className="education-body">{education.university}</div>
            <div className="time">{education.years}</div>
            <div className="desc">{education.desc}</div>
        </div>
    </div>)
}

class Education extends React.Component  {
    render() {
        return (
            <section id="education-section" className="education-section section">
                <h2 className="section-title">Education</h2>
                <div className="row">
                    <EducationList />
                </div>
            </section>
        )
    }
}

export default Education
