import React from "react"

function VolunteerList() {
    const volunteers = [
        {
            'organization': 'DomyDziecka.org',
            'selfname': 'Poland\'s portal of child care homes',
            'period': '05.2009 – present',
            'desc': 'Voluntary supporting non commercial project which support child care homes, where help with maintenance portal DomyDziecka.org. Implement some payment system Bluemedia for people which want support portal and child care houses.',
            'descExtras': []
        }
    ];

    return volunteers.map((volunteer) => <div className="item">
            <div className="work-place">
                <h3 className="place">{volunteer.selfname}</h3>
            </div>
            <div className="job-meta">
                <div className="title">{volunteer.organization}</div>
                <div className="time">{volunteer.period}</div>
            </div>
            <div className="job-desc">
                <p>{volunteer.desc}</p>
                {volunteer.descExtras.length > 0 &&
                    <ul>
                        {volunteer.descExtras.map((descExtra) => <li>{descExtra}</li>)}
                    </ul>
                }
            </div>
        </div>
    )
}

class Volunteer extends React.Component  {
    render() {
        return (
            <section id="volunteers-section" className="volunteers-section section">
                <h2 className="section-title">Volunteer experience</h2>
                <div className="timeline">
                    <VolunteerList />
                </div>

            </section>
        )
    }
}

export default Volunteer
